<template lang="html">
  <div class="row">
    <div class="col-lg-8 col-md-8 col-sm-12">
      <form-card
        v-for="group of centerGroups"
        :group="group"
        v-model="form"
      ></form-card>
      <div class="card" v-if="$util.checkChangePassword($route.params.id)">
        <div class="card-header">
          <h6 class="title pull-left">Thay đổi mật khẩu</h6>
          <div class="pull-right">
            <a class="btn-link" @click="changePassword = !changePassword">Nhấn để thay đổi mật khẩu</a>
          </div>
        </div>
        <div class="card-body form-card" v-if="changePassword">
          <div class="row">
            <div class="col-sm-12 p-0">
              <label class="required">Mật khẩu cũ</label>
            </div>
            <div class="col-sm-12 p-0">
              <el-input
                type="password"
                placeholder="Mật khẩu cũ"
                class="full-width"
                :class="errors.has('old_password')?'border-danger':''"
                v-model="old_password"
                v-validate="'required'"
                data-vv-name="old_password"
                data-vv-as="Mật khẩu cũ"
              ></el-input>
              <span class="text-danger" v-if="errors.has('old_password')">{{ errors.first('old_password') }}</span>
            </div>
          </div>
          <div class="row">
            <div class="col-sm-12 p-0">
              <label class="required">Mật khẩu</label>
            </div>
            <div class="col-sm-12 p-0">
              <el-input
                type="password"
                placeholder="Mật khẩu"
                class="full-width"
                :class="errors.has('password')?'border-danger':''"
                v-model="password"
                v-validate="'required'"
                data-vv-name="password"
                data-vv-as="Mật khẩu"
              ></el-input>
              <span class="text-danger" v-if="errors.has('password')">{{ errors.first('password') }}</span>
            </div>
          </div>
          <div class="row">
            <div class="col-sm-12 p-0">
              <label class="required">Nhập lại mật khẩu</label>
            </div>
            <div class="col-sm-12 p-0">
              <el-input
                type="password"
                placeholder="Nhập lại mật khẩu"
                class="full-width"
                :class="errors.has('repeat_password')?'border-danger':''"
                v-model="repeat_password"
                v-validate="'required|checkpassword'"
                data-vv-name="repeat_password"
                data-vv-as="Nhập lại mật khẩu"
              ></el-input>
              <span class="text-danger" v-if="errors.has('repeat_password')">{{ errors.first('repeat_password') }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-12">
      <div class="card">
        <div class="card-header">
          <h6 class="title">Phân quyền</h6>
        </div>
        <div class="card-body form-card">
          <p v-if="$util.checkChangePassword($route.params.id)">{{form.role}}</p>
          <my-select
            v-else
            class="full-width"
            placeholder="Phân quyền"
            :attribute="roles"
            v-model="form.role_id"
            :class="errors.has('role_id')?'border-danger':''"
            v-validate="'required'"
            data-vv-name="role_id"
            data-vv-as="Phân quyền"
          >
          </my-select>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import FormCard from 'src/components/UIComponents/FormCard.vue'
import { Message, MessageBox, Input, Button } from 'element-ui';
import dataFrom from './user-form'
import { mapState } from 'vuex'
import MySelect from 'src/components/UIComponents/Select'

let _form = {}, firstGroups, secondGroups;

export default {
  components: {
    FormCard,
    ElInput: Input,
    ElButton: Button,
    Message,
    MySelect
  },
  async beforeCreate() {

    firstGroups = dataFrom[0].groups;
    secondGroups = dataFrom[1].groups;

    firstGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    secondGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })

    _form.role_id = '';
    await this.$store.dispatch('fetchRoles');
  },
  created() {
    let self = this;
    this.$validator.extend('checkpassword', {
      getMessage(field, val) {
        return 'Mật khẩu không khớp'
      },
      validate(value, field) {
        let password = self.password;
        let repeatPassword = self.repeat_password;
        return password == repeatPassword;
      }
    })
  },
  data () {
    return {
      form: _form,
      centerGroups: firstGroups,
      sideGroups: secondGroups,
      password: '',
      repeat_password: '',
      old_password: '',
      changePassword: false
    }
  },
  computed: {
    ...mapState({
      originalForm: state => state.userDetail
    }),
    roles() {
      let data = this.$store.state.roles;
      let arr = {};
      arr.options = [];
      for (let item of data) {
        arr.options.push({
          value: item.id,
          title: item.title
        });
      }
      return arr;
    },
  },
  mounted() {
    let self = this;
    let id = this.$route.params.id;
    this.$store.dispatch('fetchUserDetail', id);

    this.$store.dispatch('setPageTitle', 'Cập nhật khoản quản trị');
    this.$store.dispatch('setCurrentActions', [{
      label: 'Xóa',
      type: 'warning',
      icon: '',
      callback: this.remove
    },{
      label: 'Cập nhật',
      type: 'primary',
      icon: '',
      callback: this.save
    }])

    this.form.role_id = 1;
  },
  methods: {
    cancel() {
      this.$router.push({name: 'AllUsers'});
    },
    remove(){
      let self = this;
      MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
        confirmButtonText: 'Đồng ý',
        cancelButtonText: 'Hủy bỏ',
        type: 'warning',
        center: true
      }).then(() => {
        self.$store.dispatch('removeUser', self.form.id);
        Message({
          type: 'success',
          message: 'Đã xóa tài khoản quản trị'
        });
        self.$router.push({name: 'AllUsers'});
      }).catch(() => {
        Message({
          type: 'info',
          message: 'Hủy bỏ'
        });
      })
    },
    save() {
      let self = this;
      this.$validator.validateAll().then(function (result) {
        if (result) {
          if (self.changePassword){
            self.form.password = self.password;
          }
          self.$store.dispatch('updateUser', self.form).then(result => {
            Message({
              message: 'Chỉnh sửa thành công',
              type: 'success'
            });
          }).catch(error => {
            Message({
              message: res.message,
              type: 'error'
            });
          })
        }
      })
    }
  },
  watch: {
    originalForm(newValue, oldValue) {
      this.form = newValue
    }
  },
  destroyed(){
    this.$store.dispatch('setCurrentActions', []);
  }
}
</script>

